.timeline-separator {
  border-left: 5px solid #cdedf6;
  flex: 1 1;
}
.timeline-separator-mobile {
  height: 2em;
  border-left: 5px solid #cdedf6;
}
.WorkContent {
    visibility: visible;
    animation: cd-bounce-2 0.6s;
}

.WorkIcon {
  visibility: visible;
  animation: cd-bounce-1 0.6s;
}

.WorkContentHidden {
  visibility: hidden;
}

.WorkContentInverse {
    visibility: visible;
    animation: cd-bounce-2-inverse 0.6s;
}

.Arrow {
    border-style: dashed;
    border-color: transparent;
    border-width: 0.20em;
    display: -moz-inline-box;
    display: inline-block;
    /* Use font-size to control the size of the arrow. */
    font-size: 50px;
    height: 0;
    line-height: 0;
    position: relative;
    vertical-align: middle;
    width: 0;
    background-color:#fff; /* change background color acc to bg color */ 
    border-left-width: 0.2em;
    border-left-style: solid;
    border-left-color: #EF7B45;
    visibility: visible;
    animation: cd-bounce-2 0.6s;
}

.ArrowInverse {
    border-style: dashed;
    border-color: transparent;
    border-width: 0.20em;
    display: -moz-inline-box;
    display: inline-block;
    /* Use font-size to control the size of the arrow. */
    font-size: 50px;
    height: 0;
    line-height: 0;
    position: relative;
    vertical-align: middle;
    width: 0;
    background-color:#fff; /* change background color acc to bg color */ 
    border-right-width: 0.2em;
    border-right-style: solid;
    border-right-color: #cdedf6;
    visibility: visible;
    animation: cd-bounce-2-inverse 0.6s;
}
@keyframes cd-bounce-1 {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }

  60% {
    opacity: 1;
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}
  @keyframes cd-bounce-2 {
    0% {
      opacity: 0;
      transform: translateX(-100px);
    }
  
    60% {
      opacity: 1;
      transform: translateX(20px);
    }
  
    100% {
      transform: translateX(0);
    }
  }
  @keyframes cd-bounce-2-inverse {
    0% {
      opacity: 0;
      transform: translateX(100px);
    }
  
    60% {
      opacity: 1;
      transform: translateX(-20px);
    }
  
    100% {
      transform: translateX(0);
    }
  }

